ol,ul{list-style:none}li{display:inline-block;padding:20px 0 20px}a{text-decoration:none;position:relative;display:block;font-size:1.2rem;padding:5px 0;margin:0 0 0 30px;text-transform:uppercase;transition:color 0.1s, background-color 0.1s;color:#000;font-family:'Montserrat', sans-serif}@media only screen and (min-width: 992px){a{padding:0}}a:hover{color:#4dd0e1}a:focus,a:active{color:#00bcd4}a::before{content:'';display:block;position:absolute;top:100%;height:3px;width:100%;background-color:#00bcd4;transform-origin:center top;transform:scale(0, 1);transition:color 0.1s, transform 0.2s ease-out}a:active::before{background-color:#00bcd4}a:hover::before,a:focus::before{transform-origin:center top;transform:scale(1, 1)}.navbar-hamburger{margin-right:5%}#iconButtons{border:0px solid transparent !important}html,#root{background-color:var(--dark-background);color:var(--dark-text)}.theme-dark{--dark-text: #292929;--light-text: #F9F8F8;--dark-background: #2F4550;--light-background: #586F7C;--accent: #B8DBD9;--button-border: #B8DBD9}::selection{background:#434343;color:white}:root{--blue-background: #c2e9f6;--blue-border: #72cce3;--blue-color: #96dcee;--yellow-background: #fffaa8;--yellow-border: #f5eb71;--indigo-background: #808fc7;--indigo-border: #5d6baa;--indigo-color: #6b7abb;--gray-border: #e8e8ea;--gray-dots: #e8e8ea}@keyframes reverse{0%{left:47px;width:40px}60%{left:3px;width:80px}100%{left:3px}}@keyframes switch{0%{left:3px}60%{left:3px;width:80px}100%{left:47px;width:40px}}.toggle--checkbox{display:none}.container--toggle{display:grid;place-items:center;position:relative;grid-area:toggle}.toggle--label{width:80px;height:36px;background:#6b7abb;background:var(--indigo-color);border-radius:100px;border:3px solid #5d6baa;border:3px solid var(--indigo-border);display:flex;position:relative;transition:all 350ms ease-in}.toggle--checkbox:checked+.toggle--label{background:#96dcee;background:var(--blue-color);border-color:#72cce3;border-color:var(--blue-border)}.toggle--checkbox:checked+.toggle--label:before{animation-name:reverse;animation-duration:350ms;animation-fill-mode:forwards;transition:all 360ms ease-in;background:#fffaa8;background:var(--yellow-background);border-color:#f5eb71;border-color:var(--yellow-border);width:28px;height:28px;left:3px !important}.toggle--label:before{animation-name:switch;animation-duration:350ms;animation-fill-mode:forwards;content:'';width:28px !important;height:28px;border:3px solid #e8e8ea;border:3px solid var(--gray-border);top:1px;left:43px !important;position:absolute;border-radius:40px;background:white}.toggle--label:after{transition-delay:0ms;transition:all 250ms ease-in;position:absolute;content:'';box-shadow:#e8e8ea -15px 0 0 1px,#e8e8ea -22px 10px 0 -1px;box-shadow:var(--gray-dots) -15px 0 0 1px,var(--gray-dots) -22px 10px 0 -1px;left:74px;top:8px;width:5px;height:5px;background:transparent;border-radius:50%;opacity:1}.toggle--checkbox:checked+.toggle--label:after{transition-delay:50ms;opacity:0}.toggle--checkbox:checked+.toggle--label .toggle--label-background{width:5px;left:53px;top:13px}.toggle--checkbox:checked+.toggle--label .toggle--label-background:before{top:-4px;left:-5px;width:20px;height:5px}.toggle--checkbox:checked+.toggle--label .toggle--label-background:after{top:3px;width:20px;height:5px;left:-10px}.toggle--label-background{border-radius:5px;position:relative;background:white;left:30px;width:4px;transition:all 150ms ease-in;top:18px;height:4px}.toggle--label-background:before{content:'';position:absolute;width:4px;height:4px;top:-15px;border-radius:5px;background:white;left:-16px;transition:all 150ms ease-in}.toggle--label-background:after{content:'';position:absolute;width:4px;height:4px;left:-20px;top:4px;border-radius:5px;background:white;transition:all 150ms ease-in}

